<template>
  <div class="container">
    <h2>登入狀態</h2>
    <div v-if="user">
      <p>當前用戶：{{ user.displayName }}</p>
      <p>電子郵件：{{ user.email }}</p>
      <p>電話號碼：{{ user.phoneNumber }}</p>
      <p>剩餘時間：{{ expirationTime }}</p> <!-- 新增顯示剩餘時間的元素 -->
      <button @click="logout">登出</button>
    </div>
    <div v-else>
      <p>未登入</p>
      <button @click="login">登入</button>
    </div>
  </div>
</template>

<script>
import 'firebase/compat/auth';
import { auth } from '@/firebaseConf';
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { getStorage } from 'firebase/storage';


export default {
  data() {
    return {
      user: null,
      expirationTime: null // 新增剩餘時間的資料屬性
    };
  },
  mounted() {
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.$store.dispatch("setLlogin", true);
        const uid = user.uid;
        this.user = user;
        this.updateExpirationTime(); // 更新剩餘時間

        // 检查用户是否第一次登入，即判断 `/usr/{userId}` 文件是否存在
        this.checkUserFileExists(uid)
          .then((fileExists) => {
            if (!fileExists) {
              // 如果文件不存在，创建一个新文件
              this.createUserFile(uid);
            }
          })
          .catch((error) => {
            console.error("检查文件是否存在时出错：", error);
          });
      } else {
        alert("._.");
      }
    });

  },
  methods: {
    login() {
      alert(this.user.uid);
    },
    logout() {
      auth.signOut();
    },
    updateExpirationTime() {
      const auth = getAuth();
      auth.currentUser.getIdTokenResult()
        .then(idTokenResult => {
          this.expirationTime = idTokenResult.expirationTime;
        })
        .catch(error => {
          alert("無法獲取憑證到期時間：", error);
        });
    },
    checkUserFileExists(userId) {
      // 这里使用你所需要的 Firebase Cloud Storage 客户端 SDK 进行文件存在性检查
      // 假设你使用的是上面提到的 storage 引用
      const storage=getStorage();
      const fileRef = storage.ref(`/usr/${userId}`);

      return fileRef
        .getMetadata()
        .catch((error) => {
          if (error.code === "storage/object-not-found") {
            return false; // 文件不存在
          } else {
            throw error; // 其他错误
          }
        });
    },
    createUserFile(userId) {
      // 这里使用你所需要的 Firebase Cloud Storage 客户端 SDK 创建新文件
      // 假设你使用的是上面提到的 storage 引用
      const storage=getStorage();
      const fileRef = storage.ref(`/usr/${userId}`);
      const content = "这是一个新文件。";
      const metadata = {
        contentType: "text/plain",
      };

      fileRef
        .putString(content, "raw", metadata)
        .then(() => {
          alert("新文件创建成功！");
        })
        .catch(() => {
          alert("创建新文件时出错：");
        });
    },
  }
};
</script>

